<template>
  <div class="login-box">
    <div class="lbox">
      <div class="content">
        <h2>WELCOME</h2>
        <h4>TO WOFUTURE</h4>
        <p>只为高效学习</p>
      </div>
    </div>
    <div class="rbox">
      <form class="content ui form">
        <div class="field logo">
          <img src="~assets/img/login/logo.png">
        </div>
        <div class="field required">
          <div class="ui left icon input">
            <input type="text" placeholder="手机号" v-model="phone" name="phone" id="user-phone">
            <i class="phone icon"></i>
          </div>
        </div>
        <div class="field required">
          <div class="ui left icon input">
            <input type="password" placeholder="密码" v-model="password" name="password" id="user-password">
            <i class="lock icon"></i>
          </div>
        </div>
        <div class="inline field">
          <div class="ui checkbox">
            <input type="checkbox" tabindex="0" class="hidden" v-model="rememberMe">
            <label>记住我</label>
          </div>
          <a class="link link-primary" href="/pad/forget_password/step1" _target="blank">忘记密码</a>
        </div>
        <button class="ui primary button" @click.prevent="postLogin($event)">登录</button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
import { pathConvert } from '@/assets/js/utils'

export default {
  name: 'login-box',
  data() {
    return {
      phone: '',
      password: '',
      rememberMe: true,
    }
  },
  mounted() {
    $(this.$el.querySelectorAll('.checkbox')).checkbox()
  },
  methods: {
    postLogin(event) {
      if (bowser.chrome && bowser.ios) {
        this.phone = $('#user-phone').val().trim()
        this.passwrod = $('#user-password').val()
        if (!this.phone || !this.password) {
          this.$toasted.error('iOS chrome 上，请手动输入手机号密码。')
          return
        }
      } else {
        this.phone = this.phone.trim()
        if (!this.phone || !this.password) {
          this.$toasted.error('手机号和密码必填')
          return
        }
      }

      const reNumber = /^\d+$/
      if (!this.phone.match(reNumber)) {
        this.$toasted.error('手机号格式错误')
        return
      }
      const params = { phone: this.phone, password: this.password, rememberMe: this.rememberMe }
      const url = '/pad/j/teacher/login/'

      $(event.target).addClass('loading')

      axios.post(url, qs.stringify(params)).then(resp => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          const teacher = resp.data.teacher
          this.$toasted.info('登录成功，正在跳转...')
          if (teacher.isAdmin) {
            window.location.href = '/pad/admin/manage/apps'
          } else if (teacher.is_manager) {
            window.location.href = pathConvert('management')
          } else if (teacher.isCourseTeacher) {
            window.location.href = pathConvert('teaching')
          } else if (teacher.isHeadTeacher) {
            window.location.href = pathConvert('management', 'classes')
          } else {
            window.location.href = pathConvert('dashboard')
          }
        }
        $(event.target).removeClass('loading')
      })
    },
  },
}
</script>

<style lang="less" scoped>
@breakpoint:       738px;

.login-box {
  position: relative;
}

.lbox {
  background-image: url('~assets/img/login/bg_2.jpg');
  background-size: cover;
  color: white;

  .content {
    color: #eee;
    h2 {
      font-size: 2.5em;
      font-weight: normal;
      margin: 0;
    }
    h4 {
      font-size: 1.5em;
      font-weight: normal;
      margin: 0;
    }
    p {
      font-size: 1em;
      text-align: justify;
      letter-spacing: 1em;
    }
  }
}

.rbox {
  .content {
    .field.logo {
      img {
        width: 138px;
      }
    }
    .link.link-primary {
      color: #42A5F5;
      font-size: 12px;
      float: right;
    }
  }
}

@media (min-width: @breakpoint)
{
  .login-box {
    margin: ~"calc(50vh - 45px - 150px)" auto;
    min-height: 200px;
    width: 80%;
    box-shadow: 0 1px 10px 2px rgba(0,0,0,.1);
    max-width: 768px;
    display: flex;
  }
  .lbox {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
  }
  .rbox {
    padding: 3em 2.5em;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;

    .content {
      width: 250px;
      .ui.checkbox {
        font-size: 12px;
      }
      .primary.button {
        width: 100%;
        margin: 1em 0;
      }
    }
  }

}

@media (max-width: @breakpoint)
{
  .lbox {
    display: none;
  }
  .login-box {
    width: 80%;
    margin: 3em auto;
  }
  .rbox {
    .ui.form {
      font-size: 16px;
      .field.logo {
        text-align: center;
      }
      .ui.button {
        font-size: 16px;
      }
    }
    .ui.button[type="submit"] {
      width: 100%;
    }
  }
}
</style>



