if (!String.prototype.padStart) {
  String.prototype.padStart = function padStart(targetLength, padString) {
    targetLength >>= 0 // floor if number or convert non-number to 0;
    padString = String((typeof padString !== 'undefined' ? padString : ' '))
    if (this.length > targetLength) {
      return String(this)
    }
    targetLength -= this.length
    if (targetLength > padString.length) {
      padString += padString.repeat(targetLength / padString.length) // append to original to ensure we are longer than needed
    }
    return padString.slice(0, targetLength) + String(this)
  }
}

export function pathConvert(pageName, childPath = '') {
  const pageNameMap = {
    teaching: {
      production: 'teach',
      development: 'teaching',
    },
    'classroom-manage': {
      production: 'manage/features',
      development: 'classroom-manage',
    },
    notice: {
      production: 'feature_notice',
      development: 'notice',
    },
  }

  const converter = pageNameMap[pageName] || {}
  const mappedPageName = converter[process.env.NODE_ENV] || pageName

  const indexUrl = process.env.NODE_ENV === 'development'
    ? `/static/w/pages/${mappedPageName}.html`
    : `/pad/${mappedPageName}`

  return childPath ? `${indexUrl}/${childPath}` : indexUrl
}

export class PadDateTime {
  constructor(sourceTime) {
    // sourceTime: value | string | Date Object
    this.targetTime = new Date(sourceTime)

    this.year = this.targetTime.getFullYear()
    this.month = this.targetTime.getMonth() + 1
    this.day = this.targetTime.getDate()
    this.hours = this.targetTime.getHours()
    this.minutes = this.targetTime.getMinutes()
    this.seconds = this.targetTime.getSeconds()
  }

  formatDateTime(formatString = 'YYYY-MM-DD HH:mm') {
    let timeString = `${this.hours.toString().padStart(2, '0')}:${this.minutes.toString().padStart(2, '0')}`
    if (formatString === 'YYYY-MM-DD HH:mm:ss') {
      timeString = `${timeString}:${this.seconds.toString().padStart(2, '0')}`
    }
    return `${this.formatDate()} ${timeString}`
  }
  formatDate(delimiter = '-') {
    return this.year + delimiter + this.month.toString().padStart(2, '0') + delimiter + this.day.toString().padStart(2, '0')
  }

  formatTimeDelta(otherTime, formatType = 'default') {
    // default: d天 HH:mm:ss
    // type1: d天HH时mm分
    if (otherTime && !(otherTime instanceof Date)) {
      otherTime = new Date(otherTime)
    }
    if (!otherTime) {
      otherTime = new Date()
    }
    const MS_PER_DAY = 1000 * 60 * 60 * 24
    const MS_PER_HOUR = 1000 * 60 * 60
    const MS_PER_MINUTE = 1000 * 60
    const MS_PER_SECOND = 1000
    let timeDiff = Math.abs(this.targetTime.getTime() - otherTime.getTime())

    const days = Math.floor(timeDiff / MS_PER_DAY)
    timeDiff -= days * MS_PER_DAY

    const hours = Math.floor(timeDiff / MS_PER_HOUR)
    timeDiff -= hours * MS_PER_HOUR

    const minutes = Math.floor(timeDiff / MS_PER_MINUTE)
    timeDiff -= minutes * MS_PER_MINUTE

    const seconds = Math.floor(timeDiff / MS_PER_SECOND)

    if (formatType === 'default') {
      const timeString = `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`
      return days >= 1 ? `${days}天 ${timeString}` : timeString
    }
    if (formatType === 'type1') {
      const timeString = `${hours.toString().padStart(2, '0')}时${(minutes + 1).toString().padStart(2, '0')}分`
      return days >= 1 ? `${days}天 ${timeString}` : timeString
    }
  }
}

export function isDataURL(s) {
  const dataUrlRegex = /^\s*data:([a-z]+\/[a-z]+(;[a-z\-]+\=[a-z\-]+)?)?(;base64)?,[a-z0-9\!\$\&\'\,\(\)\*\+\,\;\=\-\.\_\~\:\@\/\?\%\s]*\s*$/i
  return !!s.match(dataUrlRegex)
}



