import Vue from 'vue'
import Raven from 'raven-js'
import RavenVue from 'raven-js/plugins/vue'
import axios from 'axios'

import Toasted from 'vue-toasted'
import LoginBox from './login-box'

require('./index.less')

Vue.config.productionTip = false

Vue.use(Toasted, { duration: 1000, position: 'bottom-center' })

axios.defaults.headers.common.X_REQUESTED_WITH = 'XMLHttpRequest'
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN'
axios.defaults.xsrfCookieName = 'csrftoken'
Vue.prototype.$raven = Raven
if (process.env.NODE_ENV === 'production') {
  Raven.config('http://216e3d581cd9424fad7f5d8c046b2f64@sentry.xdf100.com/6').addPlugin(RavenVue, Vue).install()
}


new Vue({
  delimiters: ['${', '}'],
  el: '#app',
  data: {},
  components: { LoginBox },
})



